.contact{
    display : flex;
    flex-direction : column;
    justify-content : center;
    align-items : center;
    gap: 60px;
    margin : 80px 170px;
}

.contact-title{
    position: relative;
}
.contact-title h1{
    padding : 0px 30px;
    font-size : 80px;
    font-weight : 600;
}

.contact-title img{
    position : absolute;
    bottom : 0;
    right : 0;
    z-index : -1
}

.contact-section{
    display : flex;
    gap: 150px;
}

.contact-left{
    display : flex;
    flex-direction : column;
    gap : 30px;
}
.contact-left h1{
    font-size : 60px;
    font-weight : 700;
    background : linear-gradient(270deg,#df8908 80%,#b415ff 100%);
    background-clip : text;
    -webkit-background-clip : text;
    -webkit-text-fill-color : transparent;
}

.contact-left p{
    max-width : 550px;
    color : white;
    font-size : 15px;
    line-height : 30px;   
}

.contact-details{
    display : flex;
    flex-direction : column;
    gap : 30px;
    color : white;
    font-size : 20px;
}

.contact-detail{
    display : flex;
    align-items : center ;
    gap : 20px
}

.contact-right{
    display : flex;
    flex-direction : column;
    align-items : start;
    gap : 30px
}

.contact-right label{
    color : #d8d8d8;
    font-size : 25px;
    font-weight : 500px;
}

.contact-right input{
    border : none;
    width : 500px;
    height : 35px;
    padding-left : 25px;
    border-radius : 4px;
    background: #32323c;
    color : #a0a0a0;
    font-family : Outfit;
    font-size : 20px;
}

.contact-right textarea{
    width : 500px;
    height : 50px;
    border : none;
    padding : 20px;
    border-radius : 4px;
    background : #32323c;
    color : #a0a0a0;
    font-family : Outfit;
    font-size : 20px;
}

.contact-submit{
    border : none;
    color : white;
    border-radius : 50px;
    background : linear-gradient(270deg,#df8908 -5.09%, #b415ff 106.2%);
    font-size : 20px;
    padding : 15px 40px;
    margin-bottom : 30px;
    cursor : pointer;
    transition : 0.3s;
}

.contact-submit:hover{
    transform : scale(1.08);
    transition : 0.3s;
}

