.about{
    display : flex;
    flex-direction : column;
    align-items : center;
    justify-content: center;
    gap : 60px;
    margin : 80px 170px
}

.about-title{
    position : relative;
}

.about-title h1{
    padding : 0px 30px;
    font-size : 80px;
    font-weight: 600;
}

.about-title img{
    position : absolute;
    bottom : 0;
    right : 0;
    z-index : -1;
}

.about-section{
    display : flex;
    gap : 80px
}

.about-right{
    display : flex;
    flex-direction : column;
    gap : 40px;
}

.about-para{
    display : flex;
    flex-direction: column;
    font-size : 20px;
    font-weight : 400;
    gap : 20px;
}

.about-skills{
    display : flex;
    flex-direction : column;
    gap : 15px;
}

.about-skill{
    display : flex;
    gap : 20px;
    align-items: center;
    transition: 0.3s;
}

.about-skill{
    min-width : 150px;
    font-size : 18px;
    font-weight : 400;
}

.about-skill hr{
    outline : none;
    border : none;
    height : 8px;
    width : 50%;
    border-radius : 50px;
    background : linear-gradient(264deg, #df8908 -5.09%, #b415ff 106.2%);
}
.about-left img{
    border-radius: 5px;
}

.about-achievements{
    display : flex;
    justify-content :space-around;
    margin-bottom : 60px;
    width : 100%
}
.about-achievevment{
    display : flex;
    flex-direction: column;
    justify-content : center;
    align-items: center;
    gap : 10px;
    transition : 0.5s;

}

.about-achievevment h1{
    font-size : 60px;
    font-weight : 700;
    background : linear-gradient(270deg ,#df8908 50.41%, #b415ff 90.41%);
    background-clip : text;
    -webkit-background-clip : text;
    -webkit-text-fill-color: transparent;
}

.about-achievevment p{
    font-size : 28px;
    font-weight: 500;

}

.about-skill:hover{
    transform : scale(1.08);
    transition : 0.3s
}

.about-achievevment:hover{
    transform : scale(1.2);
    transition: 0.3s;
}