.navbar{
    display : flex;
    align-items: center;
    justify-content: space-between;
    margin : 20px  70px;
}
.logo{
    width : 50px;
    height: 50px;
}

.nav-menu{
    display : flex;
    align-items: center;
    list-style : none;
    gap : 60px;
    font-size: 20px;
}

.nav-connect{
    padding: 10px 20px;
    border-radius: 50px;
    background-image : linear-gradient(267deg,#DA7c25 0.36%,#B923E1 102%);
    cursor : pointer ;
    font-size: 20px;
    transition : 0.5s;
}
.nav-connect:hover{
    transform: scale(1.05);
}

.nav-menu li{
    display : flex;
    flex-direction : column;
    gap : 5px;
    cursor : pointer;
}

.nav-menu li img{
    display : flex;
    margin : auto;
}

.anchor-link{
    text-decoration : none;
    color : white;
}

.nav-mob-close{
    display : none;
}

.nav-mob-open{
    display : none;
}

@media(max-width:468px){
    .navbar{
        margin:20px 40px
    }
    .nav-connect{
        display : none;
    }
    .nav-mob-open{
        display : block;
        position : fixed;
        right : 30px;
    }
    .nav-mob-close{
        display : block;
        position : relative;
        top : 20px;
        left : 100px;
        width : 20px
    }
    .nav-menu{
        position : fixed;
        flex-direction : column;
        align-items : center;
        top : 0;
        gap : 30px;
        background-color: #1f0016;
        width : 300px;
        height : 100%;
        z-index: 2;
        transition : right 0.3s;
        right  : -330px
    }
    .nav-menu li{
        font-size : 30px;
        padding-left : 100px;
        flex-direction : row;
        gap : 20px
    }
}